import Vue from 'vue'
import Vuex from 'vuex'
import autopsyReportDefinition from '@/store/form_text/autopsy-report-hu'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    savedTemplates: [{
      "name": "Tutorial sablon",
      "text": "Ez egy példa sablon, ami bemutatja az oldal működését.\nA sablon írása közben létre lehet hozni változókat, amikhez a rendszer szövegmezőket generál.\nPéldául:\n\nNév: ${név}\nAnyja neve: ${anyja_neve}\nSzületési dátum: ${születési_dátum}\nSzületési hely: ${születési_ország}\nLakcím: ${lakcím}\n\nA változókat ${ és } karakterek közé kell írni, és a nevük nem tartalmazhat szóközt. A sablon létrehozása közben lehet hozzájuk típust rendelni, de ez a típus egyelőre nincs felhasználva semmire.\nA sablon kitöltésekor a változóknak értéket kell adni a szövegmezők segítségével.\nA sablonban még el lehet helyezni olyan részeket, amelyek elrejthetők a végleges szövegben.\nEnnek a szintaxisa a következő:\n\n${elrejthető_szöveg Ez a szövegrész csak akkor fog megjelenni a végleges dokumentumban, ha az 'elrejthető_szöveg' checkbox ki van pipálva. Az elrejthető szövegben is lehet változókat használni, akár korábbiakat, pl. név: ${név} vagy újakat: ${új_változó}. Ami fontos, hogy egyelőre nem lehet az elrejthető szövegbe egy másik elrejthetőt ágyazni.}\nViszont egy elrejthető szöveget több részre is lehet bontani.\n${elrejthető_szöveg Például ez a blokk pontosan akkor fog megjelenni, mint a kettővel felette lévő.}\n",
      "variables": [{"name": "név", "type": "string"}, {
        "name": "anyja_neve",
        "type": "string"
      }, {"name": "születési_dátum", "type": "string"}, {
        "name": "születési_ország",
        "type": "string"
      }, {"name": "lakcím", "type": "string"}, {"name": "új_változó", "type": "string"}],
      "booleanVariables": [{"name": "elrejthető_szöveg", "type": "boolean"}]
    }],
    autopsyReportDefinition
  },
  mutations: {
    saveTemplate(state, newTemplate) {
      state.savedTemplates.push(newTemplate)
    }
  },
  getters: {
    savedTemplateNames(state) {
      return state.savedTemplates.map(x => x.name)
    }
  }
})
