import Vue from 'vue'
import Router from 'vue-router'
import AutopsyReport from "./views/AutopsyReport";

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'autopsyReport',
      component: AutopsyReport
    }
  ]
})
