<template>
  <v-container fluid>
    <h1 class="subheading text--primary">{{this.$store.state.autopsyReportDefinition.title}}</h1>

    <v-expansion-panels class="mt-4">
      <v-expansion-panel>
        <v-expansion-panel-header :disable-icon-rotate="isFilled('személyes_adatok')">
          {{variableNameToLabel('személyes_adatok')}}
          <template v-slot:actions v-if="isFilled('személyes_adatok')">
            <v-icon color="teal">mdi-check</v-icon>
          </template>
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-row>
            <v-col md="3">
              <v-text-field :label="variableNameToLabel('születési_év')" type="number"
                            v-model="templateVariables.születési_év"/>
            </v-col>
            <v-col md="9">
              <v-text-field v-text="`Életkor : ${életkorString}`"/>
            </v-col>
            <v-col md="12">
              <v-radio-group v-model="templateVariables.nem" row>
                <v-radio label="Férfi" value="férfi"></v-radio>
                <v-radio label="Nő" value="nő"></v-radio>
              </v-radio-group>
            </v-col>
            <v-col md="3">
              <v-text-field :label="variableNameToLabel('testtömeg_(kg)')" type="number"
                            v-model="templateVariables['testtömeg_(kg)']"/>
              <v-text-field :label="variableNameToLabel('testmagasság_(cm)')" type="number"
                            v-model="templateVariables['testmagasság_(cm)']"/>
            </v-col>
            <v-col md="9" class="justify-center align-center">
              <v-text-field v-text="`Testtömegindex: ${testtömegindexString} ${tápláltságString}`"/>
            </v-col>
            <v-col md="3">
              <v-text-field :label="variableNameToLabel('hasfali_subcutan_zsírszövet_vastagsága_(mm)')" type="number"
                            v-model="templateVariables['hasfali_subcutan_zsírszövet_vastagsága_(mm)']"/>
            </v-col>
          </v-row>
        </v-expansion-panel-content>
      </v-expansion-panel>

      <v-expansion-panel
        v-for="variableBlock in ['szervtömegek_(g)','folyadékgyülemek_(ml)','szívbillentyű_szájadékok_kerületei_és_a_szívkamrák_vastagsága_(mm)','külvizsgálat', 'szív', 'tüdők', 'nyaki_szervek', 'lép', 'gyomor_és_a_belek', 'vesék', 'kismedencei_szervek']">
        <v-expansion-panel-header
          :disable-icon-rotate="isFilled(variableBlock)">
          {{variableNameToLabel(variableBlock)}}
          <template v-slot:actions v-if="isFilled(variableBlock)">
            <v-icon color="teal">mdi-check</v-icon>
          </template>
        </v-expansion-panel-header>

        <v-expansion-panel-content>
          <v-row>
            <v-col v-for="variable in $store.state.autopsyReportDefinition.form_panels[variableBlock]"
                   md="3" v-if="!isComputedFieldType(variable.type)">

              <v-text-field v-if="isTextFieldType(variable.type)" :label="variableNameToLabel(variable.name)"
                            :type="variable.type"
                            v-model="templateVariables[variable.name]"/>
              <v-select v-else-if="isEnumFieldType(variable.type)" :key="variable.name"
                        :label="variableNameToLabel(variable.name)"
                        :items="variable.values"
                        v-model="templateVariables[variable.name]"
                        filled/>
              <v-combobox v-else-if="isEnumWithOtherFieldType(variable.type)" :key="variable.name"
                          :label="variableNameToLabel(variable.name)"
                          :items="variable.values"
                          v-model="templateVariables[variable.name]"
                          filled/>
            </v-col>
          </v-row>
        </v-expansion-panel-content>
      </v-expansion-panel>

    </v-expansion-panels>
    <v-btn class="mt-4 teal darken-2" @click="generateText()">
      <v-icon>receipt</v-icon>
      {{this.$store.state.autopsyReportDefinition.generate_btn_text}}
    </v-btn>
    <v-card class="mt-4" v-if="reportText">
      <v-card-actions>
        <v-btn text small @click="copyTextToClipboard()">
          <v-icon>mdi-content-copy</v-icon>
        </v-btn>
      </v-card-actions>
      <v-card-text class="pt-0">
        <pre class="word-wrap">{{this.reportText}}</pre>
      </v-card-text>
    </v-card>

    <v-snackbar v-model="snackbar" :timeout="1000" bottom>
      {{this.$store.state.autopsyReportDefinition.copy_to_clipboard_msg}}
    </v-snackbar>
  </v-container>
</template>

<script>
  import TemplateProcessor from "@/model/TemplateProcessor";
  import {capitalize} from "vuetify-loader/lib/util";

  export default {
    created() {
      this.initializeTemplateVariablesFromDefaults(this.$store.state.autopsyReportDefinition.form_panels)
    },
    data() {
      return {
        templateVariables: {},
        reportText: undefined,
        snackbar: false,
      }
    },
    computed: {
      testtömegindex() {
        let heightInMetres = this.templateVariables['testmagasság_(cm)'] / 100
        return (this.templateVariables['testtömeg_(kg)'] / (heightInMetres * heightInMetres)).toFixed(1)
      },
      életkor() {
        return new Date().getFullYear() - this.templateVariables.születési_év
      },
      férfi() {
        return this.templateVariables.nem === 'férfi'
      },
      nő() {
        return this.templateVariables.nem === 'nő'
      },
      tápláltság() {
        if (!this.testtömegindex) {
          return undefined
        } else if (this.testtömegindex < 18.5) {
          return 'Sovány'
        } else if (this.testtömegindex < 24.9) {
          return 'Közepesen táplált'
        } else if (this.testtömegindex < 29.9) {
          return 'Túlsúlyos'
        } else {
          return 'Elhízott'
        }
      },
      sclerák() {
        let bőr = this.templateVariables['bőr']
        if (bőr !== undefined && (bőr.toLowerCase().startsWith('icterus') || bőr.toLowerCase().startsWith('subicterus'))) {
          return 'Sárgák'
        } else {
          return 'Szürkésfehérek'
        }
      },
      testtömegindexString() {
        return parseFloat(this.testtömegindex) < 1000 ? this.testtömegindex : ''
      },
      életkorString() {
        return this.életkor < 1000 ? this.életkor : ''
      },
      tápláltságString() {
        if (this.testtömegindexString) {
          return `(${this.tápláltság})`
        } else {
          return ''
        }
      }
    },
    methods: {
      initializeTemplateVariablesFromDefaults(formPanels) {
        for (let formPanel in formPanels) {
          formPanels[formPanel].forEach((variableDefinition) => {
            if (variableDefinition.default !== undefined) {
              this.templateVariables[variableDefinition.name] = variableDefinition.default
            }
          })
        }
      },
      variableNameToLabel(variableName) {
        return capitalize(variableName.replace(/_/g, ' '))
      },
      generateText() {
        this.templateVariables.testtömegindex = this.testtömegindex
        this.templateVariables.életkor = this.életkor
        this.templateVariables.férfi = this.férfi
        this.templateVariables.nő = this.nő
        this.templateVariables.tápláltság = this.tápláltság
        this.templateVariables.sclerák = this.sclerák

        let variableDefinitions = []
        for (let blockName in this.$store.state.autopsyReportDefinition.form_panels) {
          variableDefinitions.push(...this.$store.state.autopsyReportDefinition.form_panels[blockName])
        }

        let templateProcessor = new TemplateProcessor(this.$store.state.autopsyReportDefinition.reportTextTemplate, variableDefinitions, this.templateVariables)
        this.reportText = templateProcessor.generateText()
      },
      copyTextToClipboard() {
        navigator.clipboard.writeText(this.reportText)
        this.snackbar = true
      },
      isFilled(blockName) {
        let blockVariables = this.$store.state.autopsyReportDefinition.form_panels[blockName]
        for (let variable of blockVariables) {
          if (this.templateVariables[variable.name] === undefined && !this.isComputedFieldType(variable.type)) {
            return false
          }
        }
        return true
      },
      isTextFieldType(type) {
        return type === 'number' || type === 'string'
      },
      isEnumFieldType(type) {
        return type === 'enum'
      },
      isEnumWithOtherFieldType(type) {
        return type === 'enum_with_other'
      },
      isComputedFieldType(type) {
        return type === 'computed'
      }
    }
  }
</script>

<style scoped>
  pre.word-wrap {
    white-space: pre-wrap;
  }
</style>
