export default class TemplateProcessor {

  constructor(templateText, variableDefinitions, variablesValues) {
    this.templateText = templateText
    this.variableDefinitions = variableDefinitions
    this.variablesValues = variablesValues
    this.initAuxiliaryBooleanVariables()
  }

  /**
   * This method defines 2 auxiliary boolean variable for every non-boolean variable
   * with the following names:
   *
   * variableName_default
   * variableName_non_default
   *
   * These can be used in the template text to define what will be presented
   * in the final text if the value of the underlying variable is
   * equal to or not equal to its default value.
   */
  initAuxiliaryBooleanVariables() {
    let nonBooleanVariableDefinitions = this.variableDefinitions.filter((definition) => definition.type !== 'boolean')
    for (let variableDefinition of nonBooleanVariableDefinitions) {
      let defaultBooleanVariableName = `${variableDefinition.name}_default`
      let nonDefaultBooleanVariableName = `${variableDefinition.name}_non_default`

      this.variableDefinitions.push({'name': defaultBooleanVariableName, 'type': 'boolean'})
      this.variableDefinitions.push({'name': nonDefaultBooleanVariableName, 'type': 'boolean'})
      this.variablesValues[defaultBooleanVariableName] = this.isDefaultIsDefinedFor(variableDefinition)
      this.variablesValues[nonDefaultBooleanVariableName] = !this.isDefaultIsDefinedFor(variableDefinition)
    }
  }

  isDefaultIsDefinedFor(variableDefinition) {
    let variableValue = this.variablesValues[variableDefinition.name]
    if (variableValue == variableDefinition.default) {
      return true
    }

    return false
  }

  escapeVariableName(variableName) {
    return variableName.replace(/\(/g, '\\(').replace(/\)/g, '\\)')
  }

  generateText() {
    let processedTemplate = this.processNonBooleanVariables(this.templateText)
    processedTemplate = this.processNonBooleanVariables(processedTemplate)
    processedTemplate = this.processBooleanVariables(processedTemplate)

    return processedTemplate
  }

  processNonBooleanVariables(templateText) {
    let processedTemplate = templateText
    let nonBooleanVariableDefinitions = this.variableDefinitions.filter((definition) => definition.type !== 'boolean')
    for (let variableDefinition of nonBooleanVariableDefinitions) {
      let escapedVariableName = this.escapeVariableName(variableDefinition.name)
      let regexp = new RegExp(`\\$\\{${escapedVariableName}\\}`, 'g')
      processedTemplate = processedTemplate.replace(regexp, this.variablesValues[variableDefinition.name])
    }

    return processedTemplate
  }

  processBooleanVariables(templateText) {
    let processedTemplate = templateText
    let booleanVariableDefinitions = this.variableDefinitions.filter((definition) => definition.type === 'boolean')

    for (let booleanVariableDefinition of booleanVariableDefinitions) {
      let escapedVariableName = this.escapeVariableName(booleanVariableDefinition.name)
      // s stands for DOTALL in the RegExp
      let regexp = new RegExp(`\\$\\{${escapedVariableName} .*?(?<!\\\\)\\}`, 'gs')
      if (!this.variablesValues[booleanVariableDefinition.name]) {
        // If we have to hide the optional part then
        // we just replace all the occurrences with and empty string.
        processedTemplate = processedTemplate.replace(regexp, '')
      } else {
        // Otherwise it is a bit tricky since all occurrences have to be replaced
        // by a different string.
        let matchToReplace = regexp[Symbol.match](processedTemplate)

        while (matchToReplace) {
          let stringToReplace = matchToReplace[0]
          let newString = stringToReplace.substring(2, stringToReplace.length - 1).split(/ (.+)/s)[1]
          processedTemplate = processedTemplate.replace(stringToReplace, newString)
          matchToReplace = regexp[Symbol.match](processedTemplate)
        }
      }
    }

    return processedTemplate
  }
}
